import React, { useState, useEffect } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faEye,
  faEdit,
  faTrashAlt,
  faEllipsisH,
} from "@fortawesome/free-solid-svg-icons";
import {
  Col,
  Row,
  Card,
  Image,
  Table,
  ProgressBar,
  Dropdown,
  Button,
  ButtonGroup,
} from "@themesberg/react-bootstrap";
import { Link, useHistory } from "react-router-dom";

import axios from "axios";

const GetClient = () => {
  const [loading, setLoading] = useState(true);
  const [data, setData] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      try {
        const { data: response } = await axios.get(
          "http://localhost:3000/api/v1/users/getclients"
        );
        setData(response);
        console.log(response);
      } catch (error) {
        console.error(error.message);
      }
        setLoading(false);
    
    };

    fetchData();
  }, []);

  return (
    <>
      <Card border="light" className="shadow-sm mb-4">
        <Card.Body className="pb-0">
          <Table
            responsive
            className="table-centered table-nowrap rounded mb-0"
          >
            <thead className="thead-light">
              <tr>
                <th className="border-0"></th>
                <th className="border-0">Nom et prénom </th>
                <th className="border-0">Date de naissance </th>
                <th className="border-0">Email</th>
                <th className="border-0">Addresse </th>
                <th className="border-0">Profiles</th>
              </tr>
            </thead>
            <tbody>
              {data?.map((dm, index) => (
                <tr>
                  <td>
                    <Card.Link href="#" className="text-primary fw-bold">
                      <div className="user-avatar lg-avatar me-4">
                        <Image
                          style={{
                            flex: 1,
                            width: "50px",
                            height: "50px",
                            resizeMode: "contain",
                          }}
                          src={
                            "http://localhost:3000/uploads/" + dm.userimage
                          }
                          className="card-img-top rounded-circle border-white"
                        />
                      </div>
                    </Card.Link>
                  </td>
                  <td className="fw-bold">
                    {dm.name} {dm.lastn}
                  </td>
                  <td>{dm.Datebirth}</td>
                  <td>{dm.email}</td>
                  <td>{dm?.profiles.length}</td>
                  <td>
                    <Dropdown as={ButtonGroup}>
                      <Dropdown.Toggle
                        as={Button}
                        split
                        variant="link"
                        className="text-dark m-0 p-0"
                      >
                        <span className="icon icon-sm">
                          <FontAwesomeIcon
                            icon={faEllipsisH}
                            className="icon-dark"
                          />
                        </span>
                      </Dropdown.Toggle>
                      <Dropdown.Menu>
                        <Dropdown.Item
                        /*onClick={() => history.push("/defun/" + dm._id)}*/
                        >
                          <FontAwesomeIcon icon={faEye} className="me-2" /> View
                          Details
                        </Dropdown.Item>
                        <Dropdown.Item
                        /* onClick={() => history.push("/profile/" + dm._id)}*/
                        >
                          <FontAwesomeIcon icon={faEdit} className="me-2" />{" "}
                          Edit
                        </Dropdown.Item>
                        <Dropdown.Item className="text-danger">
                          <FontAwesomeIcon icon={faTrashAlt} className="me-2" />{" "}
                          Remove
                        </Dropdown.Item>
                      </Dropdown.Menu>
                    </Dropdown>
                  </td>
                </tr>
              ))}
            </tbody>
          </Table>
        </Card.Body>
      </Card>
    </>
  );
};
export default GetClient;
