import React, { useState, useEffect } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAngleDown, faAngleUp } from "@fortawesome/free-solid-svg-icons";
import {
  Col,
  Row,
  Card,
  Image,
  Table,
  ProgressBar,
} from "@themesberg/react-bootstrap";

import axios from "axios";
import MarkerClusterGroup from "react-leaflet-markercluster";

import L from "leaflet";
import { MapContainer, Popup, TileLayer, Marker } from "react-leaflet";
import "leaflet/dist/leaflet.css";
import "leaflet-control-geocoder/dist/Control.Geocoder.js";
import {
  buildStyles,
  CircularProgressbarWithChildren,
} from "react-circular-progressbar";
import "react-circular-progressbar/dist/styles.css";


const icon = L.icon({
  iconSize: [30, 30],
  iconAnchor: [10, 41],
  popupAnchor: [2, -40],
  iconUrl: "https://i.ibb.co/dGQnpg3/t-l-chargement-1-removebg-preview.png",
});

const center = {
  lat: 45.424721,
  lng: -75.695,
};
const Getadmins = () => {
  const [loading, setLoading] = useState(true);
  const [data, setData] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      try {
        const { data: response } = await axios.get(
          "http://localhost:3000/api/v1/users/getadmins"
        );
        setData(response);
        console.log(response);
      } catch (error) {
        console.error(error.message);
      }
      setLoading(false);
    };

    fetchData();
  }, []);

  return (
    <>
      <Card border="light" className="shadow-sm mb-4">
        <Card.Body className="pb-0">
          <Table
            responsive
            className="table-centered table-nowrap rounded mb-0"
          >
            <thead className="thead-light">
              <tr>
                <th className="border-0"></th>
                <th className="border-0">Nom et prénom </th>
                <th className="border-0">Date de naissance </th>
                <th className="border-0">Phone</th>
                <th className="border-0">Addresse </th>
                <th className="border-0">Cimitiere</th>
              </tr>
            </thead>
            <tbody>
              {data?.map((dm, index) => (
                <tr>
                  <td>
                    <Card.Link href="#" className="text-primary fw-bold">
                      <div className="user-avatar lg-avatar me-4">
                        <Image
                          style={{
                            flex: 1,
                            width: "50px",
                            height: "50px",
                            resizeMode: "contain",
                          }}
                          src={
                           "http://localhost:3000/uploads/" + dm.userimage
                          }
                          className="card-img-top rounded-circle border-white"
                        />
                      </div>
                    </Card.Link>
                  </td>
                  <td className="fw-bold">
                    {dm.name} {dm.lastn}
                  </td>
                  <td>{dm.Datebirth}</td>
                  <td>{dm.phone}</td>
                  <td>{dm?.graveyard?.address}</td>
                  <td>
                    {dm?.graveyard.name}
                  </td>
                </tr>
              ))}
            </tbody>
          </Table>
        </Card.Body>
      </Card>

      <div className="center">
        <MapContainer center={center} zoom={13} scrollWheelZoom={true}>
          <TileLayer
            attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
            url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          />
          <MarkerClusterGroup>
            {data?.map((dm, index) => (
              <Marker
                key={index}
                icon={icon}
                position={[dm?.graveyard.Lat, dm?.graveyard.Lng]}
              >
                <Popup>
                  <div
                    className="position-relative"
                    style={{ alignItems: "center" }}
                  >
                    <div></div>
                    <div>
                      <span
                        style={{
                          backgroundColor: "#3ba266",
                          fontSize: 18,
                          marginLeft: 100,
                        }}
                        color="#3ba266"
                        className="badge badge-success"
                      >
                        {dm.graveyard?.name}
                      </span>
                    </div>
                  </div>
                  <Card.Body>
                    <Card.Subtitle
                      style={{ alignItems: "center", width: "150px" }}
                      className="mb-1"
                    >
                      {dm.Location}
                    </Card.Subtitle>
                    <Card.Text className="text-muted text-small mb-0 font-weight-light">
                      Address : {dm.graveyard?.address}
                    </Card.Text>
                    <Card.Text className="text-muted text-small mb-0 font-weight-light">
                      Responsable : {dm.name}
                    </Card.Text>
                  </Card.Body>
                </Popup>
              </Marker>
            ))}
          </MarkerClusterGroup>
        </MapContainer>
        ,
      </div>
    </>
  );
};
export default Getadmins;
