import React, {
    useState,

    useEffect,
  } from "react";
  import moment from "moment-timezone";
  import Datetime from "react-datetime";
  import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
  import { faCalendarAlt } from "@fortawesome/free-solid-svg-icons";
  import {
    Col,
    Row,
    Card,
    Form,
    Button,
    InputGroup,
  } from "@themesberg/react-bootstrap";
  import axios from "axios";
  
  const Addsuperadmin = () => {  
  
    const [formData, setFormData] = useState({
      name: "",
      lastn: "",
      Datebirth: "",
      email: "",
      sex: "",
      password: "",
      userimage: "",
      phone: "",
      role: "admin",
      gname: "",
      address: "",
    });
    const [birthday, setBirthday] = useState("");
    const config = {
      headers: {
        "Content-Type": "application/json",
      },
    };
    async function Submit() {
      formData.Datebirth = birthday;
      const mydata = new FormData();
      mydata.append("name", formData.name);
      mydata.append("lastn", formData.lastn);
      mydata.append("Datebirth", formData.Datebirth);
      mydata.append("email", formData.email);
      mydata.append("sex", formData.sex);
      mydata.append("password", formData.password);
      mydata.append("userimage", formData.userimage);
      mydata.append("phone", formData.phone);
      mydata.append("role", formData.role);
      mydata.append("gname", formData.gname);
      mydata.append("funeral_home", formData.funeral_home);
      await axios
        .post("http://localhost:3000/api/v1/users/addadmin", mydata, config)
        .then((response) => {
          console.log(response);
        })
        .catch((e) => {
          console.log(e);
        });
    }
  
    return (
      <Card border="light" className="bg-white shadow-sm mb-4">
        <Card.Body>
          <h5 className="mb-4">General information</h5>
          <Form>
            <Row>
              <Col md={6} className="mb-3">
                <Form.Group id="firstName">
                  <Form.Label>First Name</Form.Label>
                  <Form.Control
                    required
                    type="text"
                    placeholder="Enter your first name"
                    onChange={(e) =>
                      setFormData({ ...formData, name: e.target.value })
                    }
                  />
                </Form.Group>
              </Col>
              <Col md={6} className="mb-3">
                <Form.Group id="lastName">
                  <Form.Label>Last Name</Form.Label>
                  <Form.Control
                    required
                    type="text"
                    placeholder="Also your last name"
                    onChange={(e) =>
                      setFormData({ ...formData, lastn: e.target.value })
                    }
                  />
                </Form.Group>
              </Col>
            </Row>
            <Row className="align-items-center">
              <Col md={6} className="mb-3">
                <Form.Group id="birthday">
                  <Form.Label>Date de naissance</Form.Label>
                  <Datetime
                    timeFormat={false}
                    onChange={setBirthday}
                    renderInput={(props, openCalendar) => (
                      <InputGroup>
                        <InputGroup.Text>
                          <FontAwesomeIcon icon={faCalendarAlt} />
                        </InputGroup.Text>
                        <Form.Control
                          required
                          type="text"
                          value={
                            birthday ? moment(birthday).format("DD/MM/YYYY") : ""
                          }
                          placeholder="dd/mm/yyyy"
                          onFocus={openCalendar}
                        />
                      </InputGroup>
                    )}
                  />
                </Form.Group>
              </Col>
  
              <Col md={6} className="mb-3">
                <Form.Group id="gender">
                  <Form.Label>Gender</Form.Label>
                  <Form.Select
                    defaultValue="0"
                    onChange={(e) =>
                      setFormData({ ...formData, sex: e.target.value })
                    }
                  >
                    <option value="0">Gender</option>
                    <option value="F">Female</option>
                    <option value="M">Male</option>
                  </Form.Select>
                </Form.Group>
              </Col>
            </Row>
            <Row>
              <Col md={6} className="mb-3">
                <Form.Group id="emal">
                  <Form.Label>Email</Form.Label>
                  <Form.Control
                    required
                    type="email"
                    placeholder="name@company.com"
                    onChange={(e) =>
                      setFormData({ ...formData, email: e.target.value })
                    }
                  />
                </Form.Group>
              </Col>
              <Col md={6} className="mb-3">
                <Form.Group id="phone">
                  <Form.Label>Phone</Form.Label>
                  <Form.Control
                    required
                    type="number"
                    placeholder="+12-345 678 910"
                    onChange={(e) =>
                      setFormData({ ...formData, phone: e.target.value })
                    }
                  />
                </Form.Group>
              </Col>
            </Row>
            <Row>
              <Col md={6} className="mb-3">
                <Form.Group id="firstName">
                  <Form.Label>Password</Form.Label>
                  <Form.Control
                    required
                    type="password"
                    placeholder="Enter your password"
                    onChange={(e) =>
                      setFormData({ ...formData, password: e.target.value })
                    }
                  />
                </Form.Group>
              </Col>
  
              <Col md={6} className="mb-3">
                <Form.Group id="firstName">
                  <Form.Label>Image</Form.Label>
                  <Form.Control
                    required
                    type="file"
                    placeholder="Enter your password"
                    onChange={(e) =>
                      setFormData({ ...formData, userimage: e.target.files[0] })
                    }
                  />
                </Form.Group>
              </Col>
            </Row>
  
           

            <div className="mt-3">
              <Button variant="primary" onClick={(e) => Submit()}>
                Save All
              </Button>
            </div>
          </Form>
        </Card.Body>
      </Card>
    );
  };
  export default Addsuperadmin;
  