import React, { useState } from "react";
import moment from "moment-timezone";
import Datetime from "react-datetime";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCalendarAlt, faEye, faEyeSlash } from "@fortawesome/free-solid-svg-icons";
import {
  Col,
  Row,
  Card,
  Form,
  Button,
  InputGroup,
} from "@themesberg/react-bootstrap";
import axios from "axios";
import jwt_decode from "jwt-decode";
import MuiAlert from "@material-ui/lab/Alert";
import Swal from "sweetalert2";
const AddClient = () => {
  const token = JSON.parse(localStorage.getItem("token"));
  let decoded = null;
  if (token !== null) decoded = jwt_decode(token);
  const [inputList, setInputList] = useState([
    {
      profileName: "",
      profileLastName: "",
      profileDatebirth: "",
      profileDatedeath: "",
      gender: "F",
      cords: "",
      modeDeath: "I",
      profileEmail: "",
      graveyard: decoded.graveyardId,
    },
  ]);

  const handleInputChange = (e, index) => {
    const { name, value } = e.target;
    console.log(e.target);
    const list = [...inputList];
    list[index][name] = value;
    setInputList(list);
  };

  const handleRemoveClick = (index) => {
    const list = [...inputList];
    list.splice(index, 1);
    setInputList(list);
  };

  const handleAddClick = () => {
    setInputList([
      ...inputList,
      {
        profileName: "",
        profileLastName: "",
        profileDatebirth: "05/05/2000",
        profileDatedeath: "",
        gender: "F",
        cords: "",
        modeDeath: "I",
        profileEmail: "",
      },
    ]);
  };

  const [formData, setFormData] = useState({
    name: "",
    lastn: "",
    Datebirth: "",
    email: "",
    sex: "",
    password: "",
    confirmPassword: "",
    userimage: "",
    phone: "",
    address: "",
    postalcode:"", 
    role: "client",
    graveyard: decoded.graveyardId,
  });
  const [birthday, setBirthday] = useState("05/05/2000");
  const config = {
    headers: {
      "Content-Type": "application/json",
    },
  };
const Submit = async (e) => {
  e.preventDefault();
  formData.Datebirth = moment(birthday).format("DD-MM-YYYY");
  const mydata = new FormData();
  mydata.append("name", formData.name);
  mydata.append("lastn", formData.lastn);
  mydata.append("Datebirth", formData.Datebirth);
  mydata.append("email", formData.email);
  mydata.append("sex", formData.sex);
  mydata.append("password", formData.password);
  mydata.append("userimage", formData.userimage);
  mydata.append("phone", formData.phone);
  mydata.append("role", formData.role);
  mydata.append("address", formData.address);
  mydata.append("postalcode", formData.postalcode);

  mydata.append("graveyard", decoded.graveyardId);
  mydata.append("profiles", JSON.stringify(inputList));

  await axios
    .post("http://localhost:3000/api/v1/users/addclient", mydata, config)
    .then((response) => {
Swal.fire({
  position: "center",
  icon: "success",
  title: "Client ajouté avec succès",
  showConfirmButton: false,
  timer: 1500,
});
    })
    .catch((e) => {
      Swal.fire({
        position: "center",
        icon: "error",
        title: "Addresse mail existe déja",
        showConfirmButton: false,
        timer: 1500,
      });
    });
};

 const [passwordType, setPasswordType] = useState("password");
 const [passwordInput, setPasswordInput] = useState("");

 const togglePassword = () => {
   if (passwordType === "password") {
     setPasswordType("text");
     return;
   }
   setPasswordType("password");
 };

  return (
    <Card border="light" className="bg-white shadow-sm mb-4">
      <Card.Body>
        <h5 className="mb-4">Information général</h5>
        <Form onSubmit={(e) => Submit(e)}>
          <Row>
            <Col md={6} className="mb-3">
              <Form.Group id="firstName">
                <Form.Label>Nom</Form.Label>
                <Form.Control
                  required
                  type="text"
                  placeholder="Enter your first name"
                  onChange={(e) =>
                    setFormData({ ...formData, name: e.target.value })
                  }
                />
              </Form.Group>
            </Col>
            <Col md={6} className="mb-3">
              <Form.Group id="lastName">
                <Form.Label>Prénom</Form.Label>
                <Form.Control
                  required
                  type="text"
                  placeholder="Also your last name"
                  onChange={(e) =>
                    setFormData({ ...formData, lastn: e.target.value })
                  }
                />
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col md={6} className="mb-3">
              <Form.Group id="addresse">
                <Form.Label>Addresse</Form.Label>
                <Form.Control
                  required
                  type="text"
                  placeholder="Entrer votre addresse"
                  onChange={(e) =>
                    setFormData({ ...formData, address: e.target.value })
                  }
                />
              </Form.Group>
            </Col>
            <Col md={6} className="mb-3">
              <Form.Group id="postalcode">
                <Form.Label>Code Postal</Form.Label>
                <Form.Control
                  required
                  type="text"
                  placeholder="Enter votre code Postal"
                  onChange={(e) =>
                    setFormData({ ...formData, postalcode: e.target.value })
                  }
                />
              </Form.Group>
            </Col>
          </Row>
          <Row className="align-items-center">
            <Col md={6} className="mb-3">
              <Form.Group id="birthday">
                <Form.Label>Date de naissance</Form.Label>

                <Form.Control
                  className="date"
                  type="date"
                  onChange={(e) => setBirthday(e)}
                />
              </Form.Group>
            </Col>

            <Col md={6} className="mb-3">
              <Form.Group id="gender">
                <Form.Label>Sexe</Form.Label>
                <Form.Select
                  defaultValue="0"
                  onChange={(e) =>
                    setFormData({ ...formData, sex: e.target.value })
                  }
                >
                  <option value="0">Gender</option>
                  <option value="F">Female</option>
                  <option value="M">Male</option>
                  <option value="I">Indéterminé</option>
                </Form.Select>
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col md={6} className="mb-3">
              <Form.Group id="emal">
                <Form.Label>Email</Form.Label>
                <Form.Control
                  required
                  type="email"
                  placeholder="name@company.com"
                  onChange={(e) =>
                    setFormData({ ...formData, email: e.target.value })
                  }
                />
              </Form.Group>
            </Col>
            <Col md={6} className="mb-3">
              <Form.Group id="phone">
                <Form.Label>Numéro de Téléphone</Form.Label>
                <Form.Control
                  required
                  type="number"
                  placeholder="+12-345 678 910"
                  onChange={(e) =>
                    setFormData({ ...formData, phone: e.target.value })
                  }
                />
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col md={6} className="mb-3">
              <Form.Group id="firstName">
                <Form.Label>Mot de passe</Form.Label>
                <InputGroup>
                  <Button
                    size="sm"
                    style={{
                      borderColor: "transparent",
                      marginRight: 5,
                      backgroundColor: "#d7dce4",
                    }}
                    onClick={() => togglePassword()}
                  >
                    {passwordType === "password" ? (
                      <InputGroup.Text>
                        <FontAwesomeIcon icon={faEye} />
                      </InputGroup.Text>
                    ) : (
                      <InputGroup.Text>
                        <FontAwesomeIcon icon={faEyeSlash} />
                      </InputGroup.Text>
                    )}
                  </Button>
                  <Form.Control
                    type={passwordType}
                    required
                    placeholder="Entrer votre mot de passe"
                    onChange={(e) =>
                      setFormData({ ...formData, password: e.target.value })
                    }
                  ></Form.Control>
                </InputGroup>
              </Form.Group>
            </Col>
            <Col md={6} className="mb-3">
              <Form.Group id="firstName">
                <Form.Label> Confirmer votre mot de passe</Form.Label>
                <InputGroup>
                  <Button
                    size="sm"
                    style={{
                      borderColor: "transparent",
                      marginRight: 5,
                      backgroundColor: "#d7dce4",
                    }}
                    onClick={() => togglePassword()}
                  >
                    {passwordType === "password" ? (
                      <InputGroup.Text>
                        <FontAwesomeIcon icon={faEye} />
                      </InputGroup.Text>
                    ) : (
                      <InputGroup.Text>
                        <FontAwesomeIcon icon={faEyeSlash} />
                      </InputGroup.Text>
                    )}
                  </Button>
                  <Form.Control
                    type={passwordType}
                    required
                    placeholder="Confirmer votre mot de passe"
                    onChange={(e) =>
                      setFormData({
                        ...formData,
                        confirmPassword: e.target.value,
                      })
                    }
                  ></Form.Control>
                </InputGroup>
                {formData.confirmPassword !== formData.password &&
                formData.confirmPassword !== "" ? (
                  <MuiAlert severity="warning">Password doesn't match</MuiAlert>
                ) : (
                  <span></span>
                )}
              </Form.Group>
            </Col>
            <Col md={6} className="mb-3">
              <Form.Group id="firstName">
                <Form.Label>Image</Form.Label>
                <Form.Control
                  required
                  type="file"
                  placeholder="Enter your password"
                  onChange={(e) =>
                    setFormData({ ...formData, userimage: e.target.files[0] })
                  }
                />
              </Form.Group>
            </Col>
          </Row>

          <h5 className="my-4">Information du Cimetière </h5>
          <Row>
            <Col sm={4} className="mb-3">
              <Form.Group id="address">
                <Form.Label>Cimetière</Form.Label>
                <Form.Control
                  disabled
                  type="text"
                  placeholder="Enter Graveyard's name"
                  value={decoded.graveyardName}
                />
              </Form.Group>
            </Col>
          </Row>

          <h5 className="my-4">Information des profiles </h5>

          {inputList.map((x, i) => {
            return (
              <>
                <h6 className="my-4"> Profile num {i + 1} </h6>

                <Row>
                  <Col md={6} className="mb-3">
                    <Form.Group id="firstName">
                      <Form.Label>Nom</Form.Label>
                      <Form.Control
                        required
                        type="text"
                        name="profileName"
                        placeholder="Entrer votre Nom"
                        defaultvalue={x.profileName}
                        onChange={(e) => handleInputChange(e, i)}
                      />
                    </Form.Group>
                  </Col>
                  <Col md={6} className="mb-3">
                    <Form.Group id="lastName">
                      <Form.Label>Prénom</Form.Label>
                      <Form.Control
                        required
                        type="text"
                        name="profileLastName"
                        placeholder="Entrer votre prénom"
                        defaultvalue={x.profileLastName}
                        onChange={(e) => handleInputChange(e, i)}
                      />
                    </Form.Group>
                  </Col>
                </Row>
                <Row className="align-items-center">
                  <Col md={6} className="mb-3">
                    <Form.Group id="birthday">
                      <Form.Label> Date de naissance</Form.Label>

                      <InputGroup>
                        <Form.Control
                          required
                          className="date"
                          name="profileDatebirth"
                          type="date"
                          onChange={(e) => handleInputChange(e, i)}
                        />
                      </InputGroup>
                    </Form.Group>
                  </Col>
                  <Col md={6} className="mb-3">
                    <Form.Group id="birthday">
                      <Form.Label>Date de décés</Form.Label>

                      <InputGroup>
                        <Form.Control
                          
                          className="date"
                          name="profileDatedeath"
                          type="date"
                          onChange={(e) => handleInputChange(e, i)}
                        />
                      </InputGroup>
                    </Form.Group>
                  </Col>

                  <Col md={6} className="mb-3">
                    <Form.Group id="gender">
                      <Form.Label>Sexe</Form.Label>
                      <Form.Select
                        defaultValue="F"
                        name="gender"
                        onChange={(e) => handleInputChange(e, i)}
                      >
                        <option value="F">Femme</option>
                        <option value="M">Homme</option>
                        <option value="I">Indéterminé</option>
                      </Form.Select>
                    </Form.Group>
                  </Col>
                  <Col md={6} className="mb-3">
                    <Form.Group id="Emplacement du funérailles">
                      <Form.Label>Emplacement du funérailles</Form.Label>
                      <Form.Control
                        required
                        type="text"
                        placeholder="Entrer les cordonnées"
                        name="cords"
                        onChange={(e) => handleInputChange(e, i)}
                      />
                    </Form.Group>
                  </Col>

                  <Col md={6} className="mb-3">
                    <Form.Group id="gender">
                      <Form.Label>Modes de funérailles</Form.Label>
                      <Form.Select
                        defaultValue="I"
                        name="modeDeath"
                        onChange={(e) => handleInputChange(e, i)}
                      >
                        <option value="I">Inhumation (Enterrement) </option>
                        <option value="C">Crémation (Incinération)</option>
                      </Form.Select>
                    </Form.Group>
                  </Col>
                  <Col md={6} className="mb-3">
                    <Form.Group id="emal">
                      <Form.Label>Email de référence</Form.Label>
                      <Form.Control
                        required
                        name="profileEmail"
                        type="email"
                        placeholder="name@profile.com"
                        onChange={(e) => handleInputChange(e, i)}
                      />
                    </Form.Group>
                  </Col>
                </Row>
                <div className="ml-50" variant="primary">
                  {inputList.length !== 1 && (
                    <Button
                      style={{ marginBottom: 20, marginLeft: 20 }}
                      className="ml-50"
                      variant="secondary"
                      onClick={() => handleRemoveClick(i)}
                    >
                      Remove
                    </Button>
                  )}
                  {inputList.length - 1 === i && (
                    <Button
                      style={{ marginBottom: 20, marginLeft: 20 }}
                      className="ml-50"
                      variant="secondary"
                      onClick={handleAddClick}
                    >
                      Add
                    </Button>
                  )}
                </div>
              </>
            );
          })}

          <div className="mt-3">
            <Button variant="primary" type="submit" >

              Save All

            </Button>
          </div>
        </Form>
      </Card.Body>
    </Card>
  );
};
export default AddClient;
